.linkLayer,
.zoomLayer {
  position: absolute;
  height: 100%;
  width: 100%;
  -webkit-transform-origin: 0 0;
      -ms-transform-origin: 0 0;
          transform-origin: 0 0;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.board {
  position: relative;
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
  cursor: move;
  overflow: hidden;
  max-height: 100vh;
  min-height: 100vh;
  min-width: 100vw;
  max-width: 100vw;
  background-color: #eee;
}



